import React, { Component, Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import FullScreenLoader from './components/FullScreenLoader';
// import Homepage from './pages/Homepage';
// import PowerPergola from './pages/PowerPergola';
// import PowerRoof from './pages/PowerRoof';
const Homepage = React.lazy(() => import('./pages/Homepage'));
const PowerPergola = React.lazy(() => import('./pages/PowerPergola'));
const PowerRoof = React.lazy(() => import('./pages/PowerRoof'));

class App extends Component {
  render() {
    return (
      <Suspense fallback={<div><FullScreenLoader /></div>}>
        <Switch>
          <Route exact path='/solar' component={() => <Redirect to='/solar/home' />} />
          <Route exact path='/solar/home' component={() => <Homepage />} />
          <Route exact path='/solar/roof' component={() => <PowerRoof />} />
          <Route exact path='/solar/pergola' component={() => <PowerPergola />} />
        </Switch>
      </Suspense>
    )
  }
}

export default App;
